@import url(https://fonts.googleapis.com/css2?family=Ruda:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@600&family=Ruda:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@900&display=swap);
*{
  margin: 0;
  padding: 0px;

}



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Ruda', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.app {
  display: flex;

}


.chat{
    background-color: rgb(62, 67, 71);
    display: flex;
    flex-direction: column;
    flex: 0.85 1;
    height: 100vh;
}
/* @media screen and (max-width: 450px) {
    .chat{

        height: 100vh;
    }
} */
.chat__messages{
    flex: 1 1;
    overflow-y: scroll;
}
.chat__messages::-webkit-scrollbar{
    display: none;
}
.chat__input{
    padding: 15px;
    align-items: center;
    display: flex;
    background-color: rgb(56, 60, 65);
    margin: 10px;
    border-radius: 50px;
    color: rgb(182, 201, 218);
}
.chat__input > form{

    display: flex;
    flex: 1 1;
}
.chat__input > form > button{
    display: none;
}
.chat__input > form > input{
    padding: 10px;
    border: none;
    color: aliceblue;
    outline: none;
    background-color: transparent;
    flex: 1 1;
    font-size: large;
    font-weight: 700;
}
.chat__messageicon{
    display: flex;
    justify-content: space-between;
}





























.chatheader{
    display: flex;

}
.chatheader__left{
    display: flex;
    flex-direction: column;
    flex: 1 1;
}

.chatheader__left > h4{
    cursor: default;
    display: flex;
    align-items: center;
    color: rgb(195, 218, 231);
    padding: 5px;

}
.chatheader__hash{
    font-size: 30px;
    padding: 10px;
    color: rgb(99, 111, 128);
}
.chatheader__right{
    display: flex;
    color: gray;
    align-items: center;
}
.chatheader__right > .MuiSvgIcon-root{
    padding: 5px;
}
.chatheader__search{
    align-items: center;
    display: flex;
    padding: 5px;
    color: grey;
    border-radius: 20px;
    background-color: rgb(68, 68, 68);
}
.chatheader__search > input{
    padding: 10px;
    background-color: transparent;
    border: 0px;
    color: rgb(255, 249, 241);
    outline: none;
}

.message{
    display: flex;
    padding: 5px;
    padding-left: 25px;
    align-items: center;
    margin-top: 10px;
}
.message__info{
    padding-left: 10px;
    color: aliceblue;
}
.message__info > p{
    font-weight: 500;
    font-size: small;
    color: rgb(196, 173, 235);
}
.message__info > h3{
    font-weight: 500;
    font-size: large;
    padding-left: 10px;
    color: aliceblue;
}
.message__timestamp{
    font-size: xx-small;
    margin-left: 50px;
    color: gray;
}
.sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.15 1;
    height: 100vh;
    background-color: #2f3135;
    color: aliceblue;
}
/* @media screen and (max-width: 600px) {
    .sidebar{
        display: none;
    }
} */

.sidebar__top{
    display: flex;

    padding: 25px;
    align-items: center;
    justify-content: space-between;
    border-bottom: 3px solid rgba(36, 36, 36, 0.815);

}
.sidebar__channels{
    display: flex;
    flex: 1 1;
    flex-direction: column;
}
.sidebar__addchannel{
    cursor: pointer;
    color: rgb(195, 218, 231);
    padding: 5px;
    border-radius: 35px;
}
.sidebar__addchannel:hover{
    color: whitesmoke;    
    background-color:  #44474d;
}
.sidebar__channelheader{
    display: flex;
    /* justify-content: space-between; */
    padding: 7px;
    cursor: default;
    margin-right: 10px;
    /* background-color: #fff; */
    align-items: center;
    color: azure;
    border-bottom: 1px solid #26272b;
}
.sidebar__header{
    font-family: 'Cinzel', serif;
    display: flex;
    flex: 1 1;

    margin: 10px;
    align-items: center;
    /* font-family: 'Ruda', sans-serif; */
    
}

.sidebar__voice{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: gray ;
    background-color: #26272b;
}
.sidebar__voiceinfo{
    padding: 10px;
    flex: 1 1;
    cursor: default;
    color: rgba(255, 165, 162, 0.507);
}
.sidebar__voiceicon{

    color: rgba(255, 165, 162, 0.507);
}
.sidebar__voiceinfo > p{
    font-size: small;
    color: #44474d;
}
.sidebar__voiceicons{
    display: flex;
}
.sidebar__voiceicons > .MuiSvgIcon-root{
    padding-right: 15px;
}

.sidebar__profile{
    padding: 10px;
    display: flex;
    cursor: default;
    align-items: center;
}
.sidebar__profile> p {

    background-color: rgb(62, 63, 65);
    border-radius: 1000px;
    padding-left: 10px;
    display: flex;
    cursor: default;
    align-items: center;
}
.sidebar__profileinfo > p{
    font-size: smaller;
}
@media screen and (max-width: 600px) {
    .sidebar__profileinfo > h3{
        font-size: 4vw;
    }
    .sidebar__profileinfo > p{
        font-size: 2vw;
    }
}


.sidebar__profile> p:hover{
    background-color: rgb(255, 0, 0);
    color: #44474d;
}

.sidebar__profileinfo{
    padding: 8px;
    flex: 1 1;
    color: #bac2d3;
    font-size: 1.3vw;
    margin-right: 8px;
}

.sidebar__profileicons{
    color: #44474d;
}
.sidebar__profileicons > .MuiSvgIcon-root{
    margin-right: 15px;
    border-radius: 20px;
    padding: 10px;
}
.sidebar__profileicons > .MuiSvgIcon-root:hover{
    background-color: rgb(61, 61, 61);
}
.sidebar__channels{
    overflow-y: scroll;
}
.sidebar__channels::-webkit-scrollbar{
    display: none;
}
/* .sidebar__channel{
    margin-left: 20px;
} */
.sidebar__channel > h4{
    cursor: pointer;
    display: flex;
    align-items: center;
    color: rgb(195, 218, 231);
    padding: 5px;
    border-bottom: 1px solid gray;


    background-color: rgb(45, 46, 48);
}
.sidebar__channel:hover > h4{
    color: rgb(172, 183, 194);
    background-color: rgb(54, 62, 65);

}
.sidebarchannel__hash{
    font-size: 30px;
    padding: 10px;
    color: rgb(99, 111, 128);
}
.login{
    height: 100vh;
    color: rgb(222, 214, 255);
    background-color: rgb(93, 106, 224);
    display: grid;
    place-items: center;
    width: 100vw;
    font-family: 'Work Sans', sans-serif;
}
.login >.login__text > img{
    height: 200px;
    align-items: center;
    border-radius: 1000px;
}
.login__text{
    text-align: center;
    align-items: center;
    cursor: default;

}
.login > .login__text > h2{
    font-size: 80px;
    text-align: center;
}
.author{
    font-size: x-small;
}
.login> button{
    background-color: rgb(242, 248, 255);
    width: 200px;
    font-weight: 800;
}
.login> button:hover{
    background-color: rgb(91, 103, 214);
    color: rgb(41, 41, 41);

}
